@font-face {
  font-family: "InterTight";
  src: url(../fonts/InterTight-Regular.woff) format("woff supports variations"), url(../fonts/InterTight-Regular.woff2) format("woff2 supports variations");
  font-weight: 400 600;
  font-display: swap;
}
html {
  line-height: 1.15;
  -webkit-text-size-adjust: 100%;
}

main {
  display: block;
}

hr {
  box-sizing: content-box;
  height: 0;
  overflow: visible;
}

pre {
  font-family: monospace, monospace;
  font-size: 1em;
}

a {
  background-color: transparent;
}

abbr[title] {
  border-bottom: none;
  text-decoration: underline;
  text-decoration: underline dotted;
}

b,
strong {
  font-weight: 600;
}

code,
kbd,
samp {
  font-family: monospace, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}

sub {
  bottom: -0.25em;
}

sup {
  top: -0.5em;
}

img {
  border-style: none;
  display: block;
}

button,
input,
optgroup,
select,
textarea {
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
  margin: 0;
}

button,
input {
  overflow: visible;
}

button,
select {
  text-transform: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  -webkit-appearance: button;
}

button::-moz-focus-inner,
[type=button]::-moz-focus-inner,
[type=reset]::-moz-focus-inner,
[type=submit]::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

button:-moz-focusring,
[type=button]:-moz-focusring,
[type=reset]:-moz-focusring,
[type=submit]:-moz-focusring {
  outline: 1px dotted ButtonText;
}

fieldset {
  padding: 0.35em 0.75em 0.625em;
}

legend {
  box-sizing: border-box;
  color: inherit;
  display: table;
  max-width: 100%;
  padding: 0;
  white-space: normal;
}

progress {
  vertical-align: baseline;
}

textarea {
  overflow: auto;
}

[type=checkbox],
[type=radio] {
  box-sizing: border-box;
  padding: 0;
}

[type=number]::-webkit-inner-spin-button,
[type=number]::-webkit-outer-spin-button {
  height: auto;
}

[type=search] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

[type=search]::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

details {
  display: block;
}

summary {
  display: list-item;
}

template {
  display: none;
}

[hidden] {
  display: none;
}

.compensate-for-scrollbar {
  padding-right: 0 !important;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

a,
button {
  text-decoration: none;
  color: inherit;
  transition: color 0.2s ease, background-color 0.2s ease, border-color 0.2s ease;
  cursor: pointer;
  background: none;
  font-weight: inherit;
  font-size: inherit;
}

select {
  background-color: inherit;
}

blockquote {
  margin: 0;
}

button {
  border: none;
  font-family: inherit;
  margin: 0;
  padding: 0;
  font-size: inherit;
  border: none;
}

textarea {
  outline: none;
  border: none;
  resize: none;
}

textarea:focus {
  outline-width: 0;
}

button,
input,
textarea,
a {
  color: inherit;
  font-family: inherit;
  font-size: inherit;
  line-height: inherit;
}

input,
input::placeholder {
  font-size: inherit;
  font-weight: 400;
  font-family: inherit;
  border: none;
  outline: none;
}

input::placeholder {
  color: inherit;
}

picture img {
  width: 100%;
  max-width: 100%;
}

ul,
li,
h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
  padding: 0;
  margin-block-end: 0;
  margin-block-start: 0;
}

ul li {
  list-style-type: none;
}

ol {
  margin: 0;
  padding: 0;
}

ol li {
  list-style-position: inside;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  display: none;
}

*, *::after, *::before {
  padding: 0;
  margin: 0;
}

.header {
  max-width: calc(1920px - clamp(0.625rem, -0.4688rem + 2.5vw, 1.25rem) * 2);
  margin: 0 auto;
  margin-block: clamp(0.625rem, -0.4688rem + 2.5vw, 1.25rem);
}
.header .content {
  background-color: #fff;
  border-radius: 30px;
  padding: 20px;
  display: flex;
  flex-direction: column;
}
.header__row {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}
@media screen and (width > 700px) {
  .header__row:first-of-type {
    padding-block-end: 20px;
  }
}
.header__row:last-of-type {
  padding-block-start: 20px;
  position: relative;
}
.header__row:last-of-type::after {
  content: "";
  display: block;
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  height: 1px;
  background-color: #dbe5f2;
}
@media screen and (width <= 1170px) {
  .header__row:last-of-type {
    justify-content: center;
  }
}
@media screen and (width <= 700px) {
  .header__row:last-of-type {
    display: none;
  }
}
.header__phone-link {
  display: block;
  padding: 18px 20px 17px;
  background-color: #f3f9ff;
  border-radius: 10px;
  color: #4995ec;
  font-weight: 600;
  line-height: 1.4;
  font-size: inherit;
  outline: 1px solid transparent;
  transition: outline 300ms ease-out;
}
.header__phone-link:hover, .header__phone-link:focus, .header__phone-link:active {
  outline: 1px solid #4995ec;
}
@media screen and (width <= 1170px) {
  .header__phone-link {
    display: none;
  }
}
.header__phone-link_mobile {
  display: none;
  font-size: clamp(1rem, 0.6618rem + 1.5686vw, 1.25rem);
  padding: 11px 20px;
  font-weight: 500;
}
@media screen and (width <= 700px) {
  .header__phone-link_mobile {
    display: block;
  }
}
@media screen and (width <= 330px) {
  .header__phone-link_mobile {
    font-size: 14px;
  }
}
.header__menu {
  display: flex;
  align-items: center;
  justify-content: center;
}
.header__btns-container {
  display: flex;
  align-items: center;
  gap: 5px;
}
@media screen and (width <= 700px) {
  .header__btns-container {
    gap: 10px;
  }
  .header__btns-container .btn_social {
    display: none;
  }
  .header__btns-container .btn-popup {
    display: none;
  }
}
@media screen and (width <= 350px) {
  .header__btns-container {
    gap: 5px;
  }
}
.header .btn-popup {
  font-weight: 600;
}
.header__address {
  display: flex;
  align-items: center;
  gap: 15px;
}
.header__address__container {
  font-size: inherit;
  line-height: 1.4;
  font-weight: 400;
  color: #133a66;
}
.header__address__title {
  font-weight: 600;
  color: #4995ec;
}
.header__address__caption {
  opacity: 0.65;
}
@media screen and (width <= 950px) {
  .header__address {
    display: none;
  }
}
@media screen and (width <= 1170px) {
  .header__search {
    display: none;
  }
}
@media screen and (width <= 700px) {
  .header .logo__title {
    display: none;
  }
}

.menu__list {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  gap: 30px;
}
.menu__item {
  color: inherit;
  line-height: 1.2;
  transition: color 200ms;
}
.menu__item:hover {
  color: #4995ec;
}
.menu_point {
  display: flex;
  align-items: center;
  gap: 10px;
}
.menu_point::before {
  content: "";
  display: block;
  background-image: url(/assets/img/icons/cuboid.svg);
  background-position: center center;
  background-repeat: no-repeat;
  width: 13px;
  aspect-ratio: 1/1;
  transition: transform 400ms ease-in-out;
}
.menu_point:hover::before {
  transform: rotate(135deg);
}
.menu_point:active::before {
  transform: rotate(135deg);
}
.menu__btn_mobile {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 50px;
  aspect-ratio: 1/1;
  background-color: #f3f9ff;
  border-radius: 10px;
  position: relative;
  transition: background-color 350ms ease-in-out, outline 300ms;
}
.menu__btn_mobile svg {
  width: 19px;
  aspect-ratio: 1/1;
  transform: rotate3d(0, 0, 0, 0deg);
  transition: transform 500ms ease-in-out, fill 300ms;
}
.menu__btn_mobile:hover, .menu__btn_mobile:active {
  background-color: #4995ec;
}
.menu__btn_mobile:hover svg, .menu__btn_mobile:active svg {
  transform: rotate3d(1, 1, 1, 360deg);
}
.menu__btn_mobile:hover svg path, .menu__btn_mobile:active svg path {
  fill: #fff;
}
.menu__btn_mobile:focus {
  outline: 1px solid #4995ec;
}
@media screen and (width > 700px) {
  .menu__btn_mobile {
    display: none;
  }
}

.search {
  width: 20%;
  height: 100%;
  position: relative;
  line-height: 1.4;
  font-size: 16px;
  border-radius: 10px;
  transition: outline 250ms;
  outline: 1px solid transparent;
}
.search:focus, .search:active {
  outline: 1px solid #4995ec;
}
.search__form {
  padding: 10px;
  position: relative;
  background-color: #f3f9ff;
  border-radius: 10px;
  overflow: hidden;
}
.search__form::after {
  content: "";
  display: block;
  position: absolute;
  top: 17px;
  right: 20px;
  width: 18px;
  aspect-ratio: 1/1;
  background-image: url(/assets/img/icons/search.svg);
}
.search__input {
  width: 100%;
  padding: 4px 6px 4px;
  position: relative;
  background-color: inherit;
}
.search__input::placeholder {
  color: rgba(19, 58, 102, 0.5);
}
.search__content {
  max-height: 0;
  overflow: hidden;
  padding: 0 10px 0;
  background-color: #f3f9ff;
  border-bottom-right-radius: 10px;
  border-bottom-left-radius: 10px;
  position: absolute;
  top: 100%;
  left: 0;
  right: 0;
  transition: max-height 650ms ease-in-out, padding 200ms;
  z-index: 5;
}
.search__content ul {
  max-height: 220px;
  overflow-y: auto;
}
.search__content ul::-webkit-scrollbar {
  width: 5px;
}
.search__content ul::-webkit-scrollbar-track {
  background-color: #f3f9ff;
}
.search__content ul::-webkit-scrollbar-thumb {
  border-radius: 5px;
  background-color: #4995ec;
}
.search__hints {
  border-top: 1px solid #dfeaf6;
  padding-block: 14px 20px;
}
.search__hints ul {
  display: flex;
  flex-direction: column;
  gap: 10px;
}
.search__offers {
  background-color: #fff;
  border-radius: 10px;
  padding: 16px;
}
.search__offers__title {
  opacity: 0.65;
  margin-block-end: 16px;
}
.search__offers ul {
  display: flex;
  flex-direction: column;
  gap: 20px;
}
.search.open .search__content {
  max-height: 500px;
  padding: 0 10px 10px;
}
.search.open .search__form {
  border-bottom-right-radius: 0;
  border-bottom-left-radius: 0;
}

html {
  scroll-behavior: smooth;
}

body {
  font-family: "InterTight";
}

body {
  min-inline-size: 285px;
  margin: 0 auto;
  font-family: sans-serif;
  letter-spacing: 0;
  font-size: clamp(1rem, 0.7143rem + 0.5714vw, 1.25rem);
  font-weight: 400;
  background-color: #e5edf7;
  color: #133a66;
  overflow-x: hidden;
}

.page {
  padding-inline: clamp(0.625rem, -0.4688rem + 2.5vw, 1.25rem);
}

@supports (font-variation-settings: normal) {
  body {
    font-family: "InterTight", sans-serif;
  }
}
.logo {
  display: flex;
  gap: 10px;
  align-items: center;
  justify-content: center;
}
.logo__picture {
  flex-shrink: 0;
}
.logo__picture img {
  max-width: 35px;
  aspect-ratio: 1/1;
  object-fit: contain;
  object-position: center;
}
.logo__title {
  font-size: 24px;
  line-height: 1.2;
  color: #4995ec;
  font-weight: 500;
}

.btn {
  border-radius: 10px;
  padding: 12px 16px 15px;
  transition: background-color 400ms, color 400ms, outline 300ms ease-out;
  outline: 2px solid transparent;
}
.btn_social {
  cursor: pointer;
  outline: 1px solid transparent;
  transition: outline 300ms ease-out, scale 300ms ease-out;
}
.btn_social:hover {
  outline: 1px solid #4995ec;
  scale: 1.015;
}
.btn_accent {
  background-color: #4995ec;
  color: #fff;
}
.btn_accent:hover, .btn_accent:active {
  background-color: #f3f9ff;
  outline: 1px solid #4995ec;
  color: #4995ec;
}
.btn_light {
  background-color: #fff;
  color: #4995ec;
}
.btn_light:hover, .btn_light:active {
  background-color: #f3f9ff;
  outline: 1px solid #4995ec;
  color: #4995ec;
}
.btn span {
  margin-inline-start: 20px;
  font-weight: 600;
  font-size: clamp(1rem, 0.2553rem + 1.7021vw, 1.5rem);
  line-height: 1.4;
}
.btn .point-mark {
  background-color: rgba(255, 255, 255, 0.2);
}
.btn .point-mark::before {
  transition: transform 300ms ease-in-out;
}
.btn:hover .point-mark::before {
  transform: rotate(90deg);
}

.point-mark {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 50px;
  aspect-ratio: 1/1;
  background-color: #f3f9ff;
  border-radius: 10px;
  position: relative;
}

.point-map::before {
  content: "";
  position: absolute;
  left: calc(50% - 10px);
  top: calc(50% - 10px);
  background-position: center center;
  background-repeat: no-repeat;
  width: 20px;
  aspect-ratio: 1/1;
  background-image: url(/assets/img/icons/point-map.svg);
}

.point-tel::before {
  content: "";
  position: absolute;
  left: calc(50% - 10px);
  top: calc(50% - 10px);
  background-position: center center;
  background-repeat: no-repeat;
  width: 20px;
  aspect-ratio: 1/1;
  background-image: url(/assets/img/icons/tel.svg);
}

.point-tg::before {
  content: "";
  position: absolute;
  left: calc(50% - 10px);
  top: calc(50% - 10px);
  background-position: center center;
  background-repeat: no-repeat;
  width: 20px;
  aspect-ratio: 1/1;
  background-image: url(/assets/img/icons/tg.svg);
}

.point-wa::before {
  content: "";
  position: absolute;
  left: calc(50% - 10px);
  top: calc(50% - 10px);
  background-position: center center;
  background-repeat: no-repeat;
  width: 20px;
  aspect-ratio: 1/1;
  background-image: url(/assets/img/icons/wa.svg);
}

.point-plus::before {
  content: "";
  position: absolute;
  left: calc(50% - 10px);
  top: calc(50% - 10px);
  background-position: center center;
  background-repeat: no-repeat;
  width: 20px;
  aspect-ratio: 1/1;
  background-image: url(/assets/img/icons/plus.svg);
}

.point-menu::before {
  content: "";
  position: absolute;
  left: calc(50% - 10px);
  top: calc(50% - 10px);
  background-position: center center;
  background-repeat: no-repeat;
  width: 20px;
  aspect-ratio: 1/1;
  background-image: url(/assets/img/icons/cuboid.svg);
}

.point-card::before {
  content: "";
  position: absolute;
  left: calc(50% - 10px);
