}

.social-container {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
  font-size: clamp(1.25rem, 1.0634rem + 0.597vw, 1.5rem);
  font-weight: 600;
  line-height: 1.2;
  transition: background-color 400ms, color 200ms;
}
.social-container__point {
  cursor: pointer;
  outline: 2px solid transparent;
  transition: outline 400ms ease-out;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: clamp(0.375rem, -0.0904rem + 1.0638vw, 0.6875rem);
  background-color: #fff;
  border-radius: 50%;
}
.social-container:hover {
  color: #b1d5ff;
}
.social-container:hover__point {
  outline: 2px solid #4995ec;
  outline-offset: -1px;
}
.social-container svg {
  width: clamp(1.125rem, 0.6596rem + 1.0638vw, 1.4375rem);
}

.social-tg {
  padding: clamp(0.5rem, 0.0346rem + 1.0638vw, 0.8125rem) clamp(0.5rem, 0.0346rem + 1.0638vw, 0.8125rem) clamp(0.4375rem, -0.0279rem + 1.0638vw, 0.75rem) clamp(0.25rem, -0.2154rem + 1.0638vw, 0.5625rem);
}

.footer {
  max-width: calc(1920px - clamp(0.625rem, -0.4688rem + 2.5vw, 1.25rem) * 2);
  margin: 0 auto;
  margin-block: clamp(0.625rem, -0.4688rem + 2.5vw, 1.25rem);
  color: #fff;
}
.footer .content {
  background: linear-gradient(134.71deg, rgb(73, 149, 236) 0%, rgb(131, 202, 232) 99.866%);
  border-radius: clamp(1.5rem, -1.8511rem + 7.6596vw, 3.75rem);
  padding: clamp(1.5rem, -3.7128rem + 11.9149vw, 5rem) clamp(1.25rem, -2.4813rem + 11.9403vw, 6.25rem) 0;
}
.footer__container {
  display: flex;
  gap: 18px;
  justify-content: space-between;
  align-items: flex-start;
  margin-block-end: clamp(2.5rem, -3.0851rem + 12.766vw, 6.25rem);
}
.footer__container:last-of-type {
  align-items: center;
  margin: 0;
  padding-block: 35px 34px;
  position: relative;
}
.footer__container:last-of-type::before {
  content: "";
  display: block;
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  height: 1px;
  background-color: rgba(255, 255, 255, 0.5019607843);
}
@media screen and (width <= 800px) {
  .footer__container:last-of-type {
    padding-block: 40px 31px;
  }
}
@media screen and (width <= 1150px) {
  .footer__container:first-of-type {
    flex-wrap: wrap;
  }
}
@media screen and (width <= 800px) {
  .footer__container:first-of-type {
    display: grid;
