}
.footer__button {
  display: flex;
  gap: 40px;
  flex-direction: row;
  align-items: center;
  padding: 10px;
  margin-block-start: 40px;
}
.footer__button .point-mark {
  background-color: #f3f9ff;
}
.footer__description {
  font-size: clamp(0.875rem, 0.7817rem + 0.2985vw, 1rem);
  line-height: 1.2;
  opacity: 0.5;
}
.footer__social {
  display: flex;
  gap: clamp(1.25rem, -0.6117rem + 4.2553vw, 2.5rem);
  color: #fff;
}
.footer__social .social-container {
  transition: background-color 400ms, color 200ms;
}
.footer__social .social-container__point {
  cursor: pointer;
  outline: 2px solid transparent;
  transition: outline 400ms ease-out;
